<template>
  <div class="wodebox">
      <div class="cenboxback" ref="topwidth"><p></p></div>
      <div class="leftbox leftcolorback leftboxshow" ref="isshowhide">
          <div class="ovhide" ref="ovcen">
              <div class="ovcen">
                <div class="logoimg leftcolor1">
                    <div class="pointercss pointercsslogo">
                    <img class="logo" src="../assets/logo.png" draggable="false">
                        <h2>Vueplug</h2>
                    </div>

                    <circle-icon v-if="iscli" size="1.24x" @click="clickisshohide" class="pointercss custom-class ridhe"></circle-icon>
                    <disc-icon v-else size="1.24x" @click="clickisshohide" class="pointercss custom-class ridhe"></disc-icon>

                </div>
                <div class="shadwod" ref="sharef"></div>
                <ul class="listul">
                    <li class="parentli-have color-icon" @click="cliunfold(i)" v-for="(item,i) in leftlist" :key="i" ref="allparent" v-if="item.id">
                        <div class="parentdiv move" :ref="'refparent'+i">
                            <home-icon v-if="item.id==1" size="1.3x" class="custom-class lefticon"></home-icon>
                            <eye-icon v-else-if="item.id==2" size="1.3x" class="custom-class lefticon"></eye-icon>
                            <folder-plus-icon v-else-if="item.id==3" size="1.3x" class="custom-class lefticon"></folder-plus-icon>
                            <span class="centi">{{item.name}}</span>
                            <chevron-right-icon size=".9x" class="custom-class"></chevron-right-icon>
                        </div>
                        <ul class="sublevel hide" :ref="'subleves'+i">
                            <li v-for="(item1,j) in item.sonarr" :key="j" class="move1" :ref="'son'+i" @click="soncli($event,'son'+i,j)">
                                <circle-icon size=".7x" class="custom-class"></circle-icon>
                                <span class="centi centileft">{{item1.sonname}}</span>
                            </li>
                        </ul>
                    </li>
                    <li v-else class="titleli" @click="packUp">
                        {{item.nametype}}
                    </li>
                </ul>
            </div>
          </div>
      </div>
      <!-- 把方法传入子组件 -->
      <apptop @topdom="mountdom"></apptop>
  </div>
</template>

<script>
import apptop from './apptop.vue';
import { DiscIcon,HomeIcon,ChevronRightIcon,CircleIcon,EyeIcon,FolderPlusIcon } from 'vue-feather-icons'
export default {
    components:{
        apptop,
        DiscIcon,
        HomeIcon,
        ChevronRightIcon,
        CircleIcon,
        FolderPlusIcon,
        EyeIcon
    },
    data(){
        return{
            iscli:false,
            leftlist:[
                {name:"首页",id:1,sonarr:[
                    {sonname:'Vue插件库'},
                    {sonname:'Angular插件库'},
                    {sonname:'React插件库'},
                ]},
                {nametype:'分类一'},
                {name:"My插件",id:2,sonarr:[
                    {sonname:'Vue插件库'},
                    {sonname:'Angular插件库'},
                    {sonname:'其它'},
                    {sonname:'React插件库'},
                    {sonname:'Vue插件库'},
                    {sonname:'Angular插件库'},
                    {sonname:'其它'},
                    {sonname:'React插件库'},
                    {sonname:'Vue插件库'},
                    {sonname:'Angular插件库'},
                    {sonname:'其它'},
                    {sonname:'React插件库'},
                    {sonname:'Vue插件库'},
                    {sonname:'Angular插件库'},
                    {sonname:'其它'},
                    {sonname:'React插件库'},
                    {sonname:'Vue插件库'},
                    {sonname:'Angular插件库'},
                    {sonname:'其它'},
                    {sonname:'React插件库'},
                    {sonname:'Vue插件库'},
                    {sonname:'Angular插件库'},
                    {sonname:'其它'},
                    {sonname:'React插件库'},
                    {sonname:'Vue插件库'},
                    {sonname:'Angular插件库'},
                    {sonname:'其它'},
                    {sonname:'React插件库'},
                    {sonname:'Vue插件库'},
                    {sonname:'Angular插件库'},
                    {sonname:'其它'},
                    {sonname:'React插件库'},
                ]},
                {name:"MyPush",id:3,sonarr:[
                    {sonname:'Vue'},
                    {sonname:'Angular'},
                    {sonname:'React'},
                    {sonname:'其它'},
                ]}
            ]
        }
    },
    mounted() {
        window.addEventListener("scroll", this.handleScroll, true);
    },
    methods:{
        // 获取子组件dom
        mountdom(data){
            console.log(data,' 子组件dom');
            this.topdom=data;
        },
        cliunfold(i){
            var domset=eval("this.$refs.subleves"+i)[0];
            var dom=domset.classList;
            var domparent=eval("this.$refs.refparent"+i)[0].classList;
            if(dom.value.includes('open')){
                domset.style.maxHeight=0;
                dom.remove("open");
                domparent.remove("openparent");
            }else{
                this.packUp();
                domset.style.maxHeight=(domset.childNodes.length*42)+'px';
                dom.add("open");
                domparent.add("openparent");
            }
        },
        // 子级点击
        soncli(e,tsdom,j){
            e.stopPropagation();
            var dom=eval("this.$refs."+tsdom);
            for(var i=0; i<dom.length; i++){
                dom[i].classList.remove('sonlicli');
            }
            dom[j].classList.add('sonlicli');
        },
        // 收起
        packUp(){
            //其它收起
            var alldom=this.$refs.allparent;
            for(var i=0; i<alldom.length; i++){
                let idom=alldom[i].childNodes;
                idom[0].classList.remove("openparent");
                idom[1].style.maxHeight=0;
                idom[1].classList.remove("open");
                var sondom=idom[1].childNodes;
                for(var j=0; j<sondom.length; j++){
                    sondom[j].classList.remove('sonlicli');
                }
            }
        },
        clickisshohide(){
            var dom=this.$refs.isshowhide.classList;
            this.iscli=!this.iscli;
            if(dom.value.includes('leftboxshow')){
                dom.remove("leftboxshow");
                dom.add("leftboxhiden");
                this.$refs.topwidth.style.maxWidth="64px";
                this.topdom.style.paddingLeft="0";
            }else{
                dom.remove("leftboxhiden");
                dom.add("leftboxshow");
                this.$refs.topwidth.style.maxWidth="260px";
                this.topdom.style.paddingLeft="194px";
            }
        },
        handleScroll(){
            var scrollTop =this.$refs.ovcen.scrollTop;
            if(scrollTop>10){
                this.$refs.sharef.style.display='block';
            }else{
                this.$refs.sharef.style.display='none';
            }
        },
    },
    beforeDestroy(){
        window.removeEventListener("scroll", this.handleScroll, true);
    }
}
</script>

<style scoped lang="less">
    @import url("../less/appleft.less");
</style>